import React, { useEffect, useState } from 'react'
import '../CircleBtns_txt/CircleBtnstxt.css'

const CircleTxtBtns = ({ title, time, addTime, commit }) => {
    // const {darkMode} = useContext(PaymentContext)
    const darkMode = 1800
    const [darkModeStyle, setDarkModeStyle] = useState({buttonStyles: 'global_circle_btns', title: 'title'})
    const [timer, setTimer] = useState({title: title, setTime: parseFloat(time)})
    const date = new Date()
    const hour = date.getHours()
    const limit = 18
    var initial_max_value = 0

    const incrementTime = () => {
        addTime(timer.setTime, commit)
    }

    const te = () =>{

    }


    useEffect(() => {
        if (darkMode >= 1800 || darkMode <= 600) {
            setDarkModeStyle({
                buttonStyles: 'global_circle_btns_dark',
                title: 'title_dark'
            })
        }
        if (darkMode < 1800 || darkMode > 600) {
            setDarkModeStyle({
                buttonStyles: 'global_circle_btns',
                title: 'title'
            })
        }

        const lStorage = localStorage.getItem('max')
        if (time == 0) {
            console.log(hour)
                if(hour < limit && hour > 6){
                    const remaining_time = limit - hour
                    setTimer({...timer, setTime: `0.0${remaining_time}`})
                    localStorage.setItem('max', true)
                }
                if(hour >= limit && hour < 24){
                    const rem_time =(24 - hour) + 6
                    setTimer({...timer, setTime: `0.${rem_time}`}) // need to set condition for when # reaach above 9      
                    localStorage.setItem('max', true)   
                }
                if(hour < 6){
                    const rem_hr = 6 - hour
                    setTimer({...timer, setTime: `0.0${rem_hr}`})                
                    localStorage.setItem('max', true)
                }
                if(lStorage == true){ 
                    console.log('work3e')
                }
            
            }
        
   
        






    }, [])


    return (
        <div className={`${darkModeStyle.buttonStyles} content-align`} onClick={incrementTime}>
            <span className={darkModeStyle.title}>{timer.title}</span>
        </div>
    )
}

export default CircleTxtBtns;